import React, { useState, useEffect, useContext, useRef } from 'react';
import styled from 'styled-components';
// context
import { StateContext } from '../Root';
// constants
import { MOBILE } from '../../constants/rs-breakpoints';
// components
import Navbar from './Navbar';

const Wrapper = styled.div`
  background-color: ${props => props.bgColor ? props.bgColor : '#000'};
  transition: all 0.3s ease-in;
  color: white;
  text-align: right;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 4fr; 
  header {
    margin: 4rem 1rem;
    @media screen and (max-width: ${MOBILE}px) {
      margin: 0.2rem 1rem;
      ${props => props.shouldMinify ? 'display: flex; justify-content: space-around; align-items: center;' : 'null' }
      transition: all 0.6s ease-in;
      h1 {
        font-size: 1.6rem;
      }
      h3 {
        font-size: 0.8rem;
      }
    }
  }
  @media screen and (max-width: ${MOBILE}px) {
    grid-template-rows: auto;
  }
`;


const Sidebar = () => {
  // context
  const { state } = useContext(StateContext);

  // ref
  const ref = useRef();

  // state
  const [headerState, setHeaderState] = useState({ shouldMinify: false });

  // tracks scroll and changes the state to minify the header when user scrolls away on mobile
  const trackScroll = () => {
    // !!! bad practice !!! trying to get 'Content' component, knowing it's the next sibling of the current component
    const content = ref.current.nextElementSibling;

    /*
      a variable to keep last boolean of the shouldMinify state, 
      to prevent unnecessary state updates if no change is detected
    */
    let lastIsMinified = false;
    content.addEventListener('scroll', () => {
      let scrollTop = content.scrollTop;
      if(scrollTop > 100) {
        if(lastIsMinified === true) return;
        lastIsMinified = true;
        setHeaderState({ shouldMinify: true });
      }
      else if(scrollTop < 100) {
        if(lastIsMinified === false) return;
        lastIsMinified = false;
        setHeaderState({ shouldMinify: false });
      }
    });
  };

  useEffect(() => {
    trackScroll();
  }, []);

  return (
    <Wrapper bgColor={state.currentColor} shouldMinify={headerState.shouldMinify} ref={ref}>
      <header>
        <h1>Arman Grigoryan</h1>
        <h3>Junior &#60;&#47;web&#62; developer</h3>
      </header>
      {window.innerWidth > 480 ? <Navbar /> : null}
    </Wrapper>
  )
}

export default Sidebar;
